.navigation {
  position: relative;
}
.navigation--closed .navigation__toggle::before {
  position: absolute;
  content: "";
  width: 24px;
  height: 2px;
  background-color: #000000;
  right: 0;
  top: 0;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (max-width: 767px) {
  .navigation--closed .navigation__wrapper {
    display: none;
  }
}
.navigation--opened .navigation__toggle::before,
.navigation--opened .navigation__toggle::after {
  content: "";
  position: absolute;
  right: 0;
  top: 8px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.navigation--opened .navigation__toggle::before {
  transform: rotate(45deg);
}
.navigation--opened .navigation__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .navigation .navigation__list {
    position: absolute;
    width: 100%;
    background-color: #ffffff;
    z-index: 10;
  }
}
@media (max-width: 767px) {
  .navigation--nojs .navigation__toggle {
    display: none;
  }
  .navigation--nojs .navigation__wrapper {
    position: relative;
    display: block;
  }
}

.navigation__toggle {
  position: absolute;
  top: -43px;
  right: 20px;
  width: 24px;
  height: 18px;
  background-color: #ffffff;
  border: none;
  z-index: 2;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
}
@media (min-width: 768px) {
  .navigation__list {
    width: 440px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    width: 478px;
  }
}

.navigation__link {
  color: #000000;
  background-color: #ffffff;
  text-decoration: none;
  padding-top: 22px;
  padding-bottom: 24px;
  position: relative;
  box-sizing: border-box;
  display: block;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 1;
  color: #000000;
  text-transform: uppercase;
}
.navigation__link::before {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 1px;
  background-color: #e6e6e6;
  content: "";
}
.navigation__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 768px) {
  .navigation__link {
    padding-top: 18px;
    padding-bottom: 8px;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.33;
    color: #000000;
    white-space: nowrap;
    border-top: none;
  }
  .navigation__link::before {
    display: none;
  }
  .navigation__link--current {
    position: relative;
  }
  .navigation__link--current::after {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 0;
    height: 2px;
    background-color: #68b738;
    content: "";
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    padding-top: 70px;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.5;
    color: #111111;
  }
  .navigation__link--index {
    background-color: transparent;
    color: #ffffff;
  }
  .navigation__link--index.navigation__link--current::after {
    background-color: #ffffff;
  }
  .navigation__link--index:hover {
    color: rgba(255, 255, 255, 0.6);
  }
  .navigation__link--index:active {
    color: rgba(255, 255, 255, 0.3);
  }
}

