@use "../global/bootstrap.scss";

.programs {
  @include bootstrap.offset-sides-y(10px, 10px);

  @media (min-width: bootstrap.$tablet-width) {
    @include bootstrap.offset-sides-y(0, 15px);
  }

  @media (min-width: bootstrap.$desktop-width) {
    @include bootstrap.offset-sides-y(28px, 14px);
  }
}

.programs__list {
  @include bootstrap.list-reset;
  margin: 0 auto;
  width: 280px;
  display: grid;
  row-gap: 22px;

  @media (min-width: bootstrap.$tablet-width) {
    width: 708px;
    row-gap: 32px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    width: 1220px;
    grid-template-columns: 1fr 1fr;
    gap: 80px;
  }
}

.programs__item {
  @include bootstrap.offset-sides-y(30px, 18px);
  @include bootstrap.offset-sides-x(20px, 20px);
  position: relative;
  background-color: bootstrap.$grey;

  &--slim::before {
    position: absolute;
    background-image: url("../img/cat-slim.svg");
    background-repeat: no-repeat;
    width: 50px;
    height: 53px;
    top: 21px;
    content: "";
  }

  &--pro::before {
    position: absolute;
    background-image: url("../img/cat-pro.svg");
    background-repeat: no-repeat;
    width: 67px;
    height: 50px;
    top: 24px;
    left: 13px;
    content: "";
  }

  @media (min-width: bootstrap.$tablet-width) {
    @include bootstrap.offset-sides-y(66px, 57px);
    @include bootstrap.offset-sides-x(316px, 54px);

    &--slim::before {
      background-image: url("../img/cat-slim-tablet.svg");
      width: 200px;
      height: 211px;
      top: 81px;
      left: 446px;
    }

    &--pro::before {
      background-image: url("../img/cat-pro-tablet.svg");
      width: 268px;
      height: 200px;
      top: 91px;
      left: 412px;
    }
  }

  @media (min-width: bootstrap.$desktop-width) {
    @include bootstrap.offset-sides-y(68px, 55px);
    @include bootstrap.offset-sides-x(61px, 52px);

    &--slim::before {
      background-image: url("../img/cat-slim-desktop.svg");
      width: 100px;
      height: 106px;
      top: 41px;
      left: 52px;
    }

    &--pro::before {
      background-image: url("../img/cat-pro-desktop.svg");
      width: 134px;
      height: 100px;
      top: 47px;
      left: 33px;
    }
  }
}

.programs__title {
  margin: 0;
  margin-left: 71px;
  padding-bottom: 29px;
  font-family: bootstrap.$primary-family;
  font-weight: bootstrap.$normal;
  font-size: 24px;
  line-height: 1.5;
  color: bootstrap.$primary-dark;
  text-transform: uppercase;
  align-self: center;

  @media (min-width: bootstrap.$tablet-width) {
    margin-left: 0;
    padding-bottom: 14px;
    font-size: 36px;
  }

  @media (min-width: bootstrap.$desktop-width) {
    margin-left: 162px;
    padding-bottom: 62px;
  }
}

.programs__description {
  position: relative;
  margin: 0;
  padding-bottom: 42px;
  font-family: bootstrap.$secondary-family;
  font-weight: bootstrap.$normal;
  font-size: 14px;
  line-height: 1.29;
  color: bootstrap.$dark-grey;

  &::after {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 17px;
    height: 1px;
    background-color: bootstrap.$secondary-light-grey;
    content: "";
  }

  @media (min-width: bootstrap.$tablet-width) {
    padding-bottom: 53px;
    font-size: 16px;
    line-height: 1.5;
    color: bootstrap.$primary-dark;

    &::after {
      display: none;
    }
  }

  @media (min-width: bootstrap.$desktop-width) {
    padding-bottom: 28px;
  }
}

.programs__link {
  @include bootstrap.offset-sides-x(47px, 0);
  position: relative;
  margin-top: 15px;
  font-family: bootstrap.$primary-family;
  font-weight: bootstrap.$normal;
  font-size: 16px;
  line-height: 1;
  color: bootstrap.$primary-dark;
  text-decoration: none;
  text-transform: uppercase;

  &:active {
    opacity: 0.3;
  }

  &:hover .programs__link-icon,
  &:active .programs__link-icon {
    transform: scaleX(1.4);
  }

  @media (min-width: bootstrap.$tablet-width) {
    margin-top: 0;
    padding-right: 57px;
    font-size: 20px;
    line-height: 1.5;
  }
}

.programs__link-icon {
  position: absolute;
  margin-left: 15px;
  top: 7px;
  transform: scaleX(1);
  transform-origin: center left;
  transition: transform 0.5s;

  @media (min-width: bootstrap.$tablet-width) {
    margin-left: 25px;
    top: 10px;
  }
}
